                        <div slot="highlight" class="highlight"><pre v-pre><code v-pre class="language-html">
 <span class="hljs-tag">&lt;<span class="hljs-name">so-tag</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">so-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">so-tag</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"success"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">so-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">so-tag</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"info"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">so-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">so-tag</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"warning"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">so-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">so-tag</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"danger"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">so-tag</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3 id="ke-yi-chu-biao-qian"></h3>
<p><code v-pre>closable</code>Booleantrue </p>
