<so-tag :key="index" v-for="(tag,index) in dynamicTags" closable :disable-transitions="false" @close="handleClose(tag)" :color="tag.color">
  {{tag.name}}
</so-tag>

</div>
<demo-block class="demo-box">
                        <div slot="source" class="source"><html><head></head><body><so-tag :key="index" v-for="(tag,index) in dynamicTags" closable :disable-transitions="false" @close="handleClose(tag)" :color="tag.color">
  {{tag.name}}
</so-tag>


</body></html></div>
                        
                        <div slot="highlight" class="highlight"><pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">so-tag</span>
  <span class="hljs-attr">:key</span>=<span class="hljs-string">"index"</span>
  <span class="hljs-attr">v-for</span>=<span class="hljs-string">"(tag,index) in dynamicTags"</span>
  <span class="hljs-attr">closable</span>
  <span class="hljs-attr">:disable-transitions</span>=<span class="hljs-string">"false"</span>
  <span class="hljs-attr">@close</span>=<span class="hljs-string">"handleClose(tag)"</span> <span class="hljs-attr">:color</span>=<span class="hljs-string">"tag.color"</span>&gt;</span>
  {{tag.name}}
<span class="hljs-tag">&lt;/<span class="hljs-name">so-tag</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">dynamicTags</span>: [{
            <span class="hljs-attr">name</span>: <span class="hljs-string">''</span>,
            <span class="hljs-attr">color</span>: <span class="hljs-string">'primary'</span> 
        }, {
           <span class="hljs-attr">name</span>: <span class="hljs-string">''</span>,
           <span class="hljs-attr">color</span>: <span class="hljs-string">'success'</span> 
        }, {
           <span class="hljs-attr">name</span>: <span class="hljs-string">''</span>,
           <span class="hljs-attr">color</span>: <span class="hljs-string">'info'</span>           
        }, {
            <span class="hljs-attr">name</span>: <span class="hljs-string">''</span>,
            <span class="hljs-attr">color</span>: <span class="hljs-string">'danger'</span>
        }]
      };
    },
    <span class="hljs-attr">methods</span>: {
      handleClose(tag) {
        <span class="hljs-keyword">this</span>.dynamicTags.splice(<span class="hljs-keyword">this</span>.dynamicTags.indexOf(tag), <span class="hljs-number">1</span>);
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h4 id="jie-shou-can-shu"></h4>
<table class="table"><thead>
<tr>
<th></th>
<th></th>
<th></th>
<th></th>
<th></th>
</tr>
</thead>
<tbody>
<tr>
<td>name</td>
<td></td>
<td>Boolean</td>
<td></td>
<td>false</td>
</tr>
<tr>
<td>color</td>
