<td><code v-pre>primary</code>, <code v-pre>success</code>, <code v-pre>error</code>, <code v-pre>warning</code>, <code v-pre>info</code></td>
<td>primary</td>
</tr>
<tr>
<td>closable</td>
<td></td>
<td>Boolean</td>
<td></td>
<td>false</td>
</tr>
</tbody>
</table>
<h4 id="bang-ding-shi-jian"></h4>
<table class="table"><thead>
<tr>
<th></th>
<th></th>
<th></th>
</tr>
</thead>
<tbody>
<tr>
<td>close</td>
<td></td>
<td>event</td>
</tr>
</tbody>
</table>
</section></template>
<style>
  .sy__tag{
    margin-right: 20px;
  }
</style>
<script>
export default {
    data() {
      return {
        dynamicTags: [{
           name: '',
           color: 'primary' 
        }, {
           name: '',
           color: 'success' 
        }, {
           name: '',
           color: 'info'           
        }, {
            name: '',
            color: 'danger'
        }]
      };
    },
    methods: {
      handleClose(tag) {
        this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1);
      }
    }
  }
</script>


